@import url('https://fonts.googleapis.com/css2?family=Graduate&family=Lato:wght@300;400&display=swap');
body {
    margin: 0;
    padding: 0;
    min-width: 480px;
    min-height: 100vh;
    background-size: cover;
    transition: background-image 1s ease-in-out;
    color: white;
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
        z-index: -1;
    }
    .links {
        position: absolute;
        bottom: 20px;
        left: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 150px;
        #github_link img {
            width: 50px;
        }
        #rs_link img {
            margin-left: 10px;
            width: 100px;
        }
    }
    .change_language {
        position: absolute;
        width: 100px;
        height: 100px;
        bottom: 20px;
        right: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 40px;
        cursor: pointer;
        background-color: #710707;
        z-index: 0;
        border-radius: 100%;
        transition: 0.3s linear;
        &:hover {
            background-color: #9D8665
        }
    }
    .container {
        width: 98%;
        overflow: hidden;
    }
    header, footer, main {
        display: flex;
        justify-content: center;
        width: 100%;
    }
    .header_container {
        height: 30vh;
        min-height: 220px;
        display: flex;
        justify-content: space-between;
        #audio_player {
            margin-top: 20px;
            .audio_controls {
                display: flex;
                align-items: center;
                margin-bottom: 30px;
                .audio_buttons {
                    background-color: rgba(0,0,0,0);
                    cursor: pointer;
                    border: none;
                    path {
                        fill: rgb(179, 176, 176);
                        transition: 0.3s linear;
                    }
                    &:hover {
                        path {
                            fill: #fff;
                        }
                    }
                }
                .duration {
                    width: 250px;
                    margin-left: 20px;
                    
                    #duration_slider {
                        width: 100%;
                    }
                    input {
                        cursor: pointer;
                    }
                }
                .audio_time {
                    margin-left: 20px;
                    
                }
                .volume {
                    margin-left: 20px;
                    input {
                        cursor: pointer;
                    }
                }
                #sound_off {
                    margin-left: 20px;
                    cursor: pointer;
                    path {
                        fill: rgb(179, 176, 176);
                        transition: 0.3s linear;
                    }
                    &:hover {
                        path {
                            fill: #fff;
                        }
                    }
                }
            }
            .audio_list {
                list-style: none;
                padding: 0;
                .list_item {
                    cursor: pointer;
                    font-family: 'Lato', sans-serif;
                    font-size: 18px;
                    margin-top: 10px;
                    color: rgb(179, 176, 176);
                    transition: 0.3s linear;
                    &:before {
                        content: "\2714";
                    }
                    &:hover {
                        color: white
                    }
                }
                .active {
                    color: #9D8665
                }
            }

        }
        #weather_bar {
            display: flex;
            flex-direction: column;
            max-width: 220px;
            #city_input {
                padding: 5px;
                font-size: 20px;
                line-height: 24px;
                margin: 20px 0 0 0;
                font-family: 'Lato', sans-serif;
                height: 34px;
                background-color: rgba(0,0,0,0);
                color: white;
                outline: none;
                border: none;
                border-bottom: 1px solid white;
            }
            .temperature {
                word-wrap: break-word ;
            }
            .temperature, .wind_speed, .humidity {
                margin-top: 10px;
                font-family: 'Lato', sans-serif;
                font-size: 16px;

            }
        }
    }

    main {
        height: 40vh;
        min-height: 260px;

        .time_date_container {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            #current_time {
                h2 {
                    min-height: 124px;
                    margin: 0 0 10px 0;
                    font-family: 'Lato', sans-serif;
                    font-size: 100px;
                    letter-spacing: -4px;
                }
            }
            #current_date {
                h4{
                    font-family: 'Lato', sans-serif;
                    min-height: 28px;
                    font-size: 24px;
                    margin: 0 0 20px 0;
                }

            }
            #greeting {
                display: flex;
                justify-content: center;
                min-height: 48px;
                width: 100%;
                font-size: 40px;
                font-family: 'Lato', sans-serif;
                font-weight: 300;
                h3 {
                    margin: 0;
                    width: 50%;
                    text-align: right;
                    padding-right: 20px;
                }
                #username {
                    width: 50%;
                    background-color: rgba(0,0,0,0);
                    border: none;
                    outline: none;
                    color: white;
                }
            }
            .slide_left, .slide_right {
                background-color: rgba(0,0,0,0);
                border: none;
                cursor: pointer;

                position: absolute;
                top: 50%;
                path {
                    fill: rgb(179, 176, 176);
                    transition: 0.3s linear;
                }
                &:hover {
                    path {
                        fill: #fff;
                    }
                }
            }
            .slide_left {
                left: 0;
            }
            .slide_right {
                right: 0;
            }
        }
    }
    .footer_container {
        height: 30vh;
        min-height: 160px;
        display: flex;
        justify-content: center;

        #quote_day {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            width: 70%;
            align-items: center;
            .refresh_quote {
                background-color: rgba(0,0,0,0);
                border: none;
                cursor: pointer;
                margin-bottom: 36px;
                path {
                    fill: rgb(179, 176, 176);
                    transition: 0.3s linear;
                }
                &:hover {
                    path {
                        fill: #fff;
                    }
                }
            }
            .quote_author {
                min-height: 20px;
                margin-bottom: 20px;
                font-family: 'Lato', sans-serif;
                font-size: 18px;
                color: #fff;
                text-align: center;
            }
            .quote_text {
                margin:0;
                min-height: 45px;
                font-family: 'Lato', sans-serif;
                font-size: 22px;
                color: #fff;
                text-align: center;
            }
        }
    }
}

@media screen and (max-width: 1000px) {
    body {
        .change_language {
            width: 70px;
            height: 70px;
            font-size: 30px;
     
        }
        main {
            .time_date_container {
                #current_time {
                    h2 {
                        min-height: 100px;
                        font-size: 80px;
                    }
                }
                #current_date {
                    h4{
                        min-height: 24px;
                        font-size: 22px;
                    }
            
                }
                #greeting {
                    min-height: 40px;
                    font-size: 32px;
                }
            }
        }
        .footer_container {
            #quote_day {
                .quote_author {
                    min-height: 17px;
                    font-size: 15px;
                }
                .quote_text {
                    min-height: 40px;
                    font-size: 19px;
                }
            }
        }
    }
    
}

@media screen and (max-width: 700px) {
    body {
        .change_language {
            width: 50px;
            height: 50px;
            font-size: 25px;
     
        }
        .header_container {
            #weather_bar {
                max-width: 130px;
            }    
        }
        main {
            .time_date_container {
                #current_time {
                    h2 {
                        min-height: 90px;
                        font-size: 70px;
                    }
                }
                #current_date {
                    h4{
                        min-height: 21px;
                        font-size: 18px;
                    }
            
                }
                #greeting {
                    min-height: 30px;
                    font-size: 22px;
                }
            }
        }
        .footer_container {
            #quote_day {
                .quote_author {
                    min-height: 15px;
                    font-size: 13px;
                }
                .quote_text {
                    min-height: 38px;
                    font-size: 17px;
                }
            }
        }
    }
    
}

@media screen and (max-width: 480px) {
    body {
        .change_language {
            width: 35px;
            height: 35px;
            font-size: 18px;
     
        }
        .header_container {
            #weather_bar {
                max-width: 100px;
            }    
        }
        main {
            .time_date_container {
                #current_time {
                    h2 {
                        min-height: 70px;
                        font-size: 50px;
                    }
                }
                #current_date {
                    h4{
                        min-height: 17px;
                        font-size: 13px;
                    }
            
                }
                #greeting {
                    min-height: 30px;
                    font-size: 22px;
                }
            }
        }
        .footer_container {
            #quote_day {
                .quote_author {
                    min-height: 15px;
                    font-size: 13px;
                }
                .quote_text {
                    min-height: 38px;
                    font-size: 17px;
                }
            }
        }
    }
    
}